<template>
  <div class='personal-page'>
     <div class='header'>
      <!-- <canvas ref='canvas'></canvas> -->
     
      <router-link to="/login"> <img src="@/assets/img/user.jpg"/></router-link>
      <div class='username'><router-link to="/login" tag="span">{{user.nikename}}</router-link><i>{{user.level}}</i></div>
      <ul class='info-data'>
        <li>帖子 {{user.post}}</li>
        <li>积分 {{user.integral}}</li>
        <li>悬赏 {{user.reward}}</li>
      </ul>
     </div>
     <div class='personal-body'>
      <div class='task'>
        <span>获取积分</span>
        <ul class='task-ul'>
          <li>
            <i class='fa fa-calendar-check-o'><img src="@/assets/img/打卡.png" alt=""></i>
            打卡签到
          </li>
          <li>
            <i class='fa fa-edit'><img src="@/assets/img/打卡.png" alt=""></i>
            发表动态
          </li>
          <li>
            <i class='fa fa-user-plus'><img src="@/assets/img/打卡.png" alt=""></i>
            添加好友
          </li>
          <li>
            <i class='fa fa-reply-all'><img src="@/assets/img/打卡.png" alt=""></i>
          回复评论
          </li>
        </ul>
      </div>
      <ul class='setting'>
        <li><i class='fa fa-file-text-o'><img src="@/assets/img/帖子.png" alt=""></i>我的帖子<i class='right fa fa-chevron-right'><img src="@/assets/img/右箭头.png" alt=""></i></li>
        <li><i class='fa fa-files-o'><img src="@/assets/img/帖子.png" alt=""></i>我的订单<i class='right fa fa-chevron-right'><img src="@/assets/img/右箭头.png" alt=""></i></li>
        <li><i class='fa fa-star-o'><img src="@/assets/img/帖子.png" alt=""></i>历史收藏<i class='right fa fa-chevron-right'><img src="@/assets/img/右箭头.png" alt=""></i></li>
        <li class='list-end'><img src="@/assets/img/帖子.png" alt=""><i class='fa fa-cog'></i>设置<i class='right fa fa-chevron-right'><img src="@/assets/img/右箭头.png" alt=""></i></li>
        <li>新消息通知
        <i ref='switch' class='switch right fa' @click='switchNews()'>
          <i class='top-btn'></i>
        </i></li>
      </ul>
     </div>
    <input type="button" value="退 出 登 录" id='my-btn' @click="logout">
  </div>
</template>

<script>
import router from '../plugins/router';
export default {
  data(){
    return {
      user:{}
    }
  },
  // 前置守卫
  beforeRouteEnter(to,from,next){
    // 异步条件
    axios({
      url:'/api/user'
    }).then(
      res=>{
        if(res.data.err === 0){
          next(_this=>_this.user = res.data.data)
        }else{
          // this.$router.push('/login')  ×
          // router.push('/login')
          next('/login')
        }
      }
    )
  },
  methods:{
    // 注销·退出登录
    logout(){
      axios({
        url:"/api/logout",
        method:'delete'
      }).then(
        res=>res.data.err === 0 && this.$router.push('/login')
      )
    }
  }





}
</script>




<style>
  .personal-page{padding-bottom: 4.5rem;}
     .personal-page .header{
       padding-top: 1.3rem;
      height: 10rem;
      overflow: hidden;
      text-align: center;
       background:#f99;
       font-size: 0.8rem;}
      /* .personal-page .header canvas{
        width: 100%;
       
      } */
      .personal-page .header img{
        position: relative;
