        margin: 0 auto;
      }
      .personal-page .header .username{
        position:relative;
        margin:0.5rem;
        color:#fff;}
         .personal-page .header .username i{
          display: inline-block;
          width: 50px;
          border: 1px solid gold;
          border-radius: 15px;
          color: gold;
          margin-left: 12px;
        }
      
       .personal-page .header .info-data{
        position: relative;
        display: flex;
