          list-style:none;
          width:33%;
          border-right: 1px solid rgba(255,255,255,.8);
        }
         .personal-page .header .info-data li:nth-child(3){
          border-right:none;
        }
      
    
    /* .personal-page .personal-body{} */
      .personal-page .personal-body .task{
        padding:0.7rem 10px;
        border-bottom: 8px solid #f1f1f1;
        font-size: 0.9rem;}
         .personal-page .personal-body .task .task-ul{
          text-align: center;
          margin-top:10px;}
         .personal-page .personal-body .task .task-ul li{
            display:inline-block;
            list-style:none;
            width:22%;
            font-size: 0.7rem;
            
            }
            .personal-page .personal-body .task .task-ul li .fa{
              width: 2rem;
              height: 0.7rem;
              margin: 0 auto;
              padding: 13px 0px;
              border-radius: 50%;
              display:block;
              color:#fff;
              margin-bottom: 6px;
              
            }
            .fa-calendar-check-o{
              background:#07c7ba;
            }
            .fa-edit{
              background:#FF69B4;
            }
            .fa-user-plus{
              background:gold;
            }
            .fa-reply-all{
              background:#f37936;
            }
     
      /* .personal-page .personal-body .task .setting{} */
        .personal-page .personal-body .setting li{
          height: 3rem;
          line-height: 3rem;
          padding: 0 10px;
          border-bottom: 1px solid #f1f1f1;
          font-size: 0.9rem;
          color:#666;}
          .personal-page .personal-body .setting li .fa{
            margin-right: 10px;
            color: #999;
          }
          .personal-page .personal-body .setting li .right{
            float: right;
           
          }
          .personal-page .personal-body .setting li .switch{
            margin-top: 0.5rem;
            width: 60px;
            height: 30px;
            border: 1px solid #dadada;
            border-radius: 20px;
            background:#f1f1f1;}
             .personal-page .personal-body .task .setting li .switch .top-btn{
              display: block;
              height: 30px;
              width: 30px;
              background: #fff;
              border-radius: 50%;
              position: relative;
              left: 30px;
              transition: left 0.5s;
              border:none;
            }
          
          /* .on{
            background: #32c532;
            border: none;
            .top-btn{
              left:0px;
              border: 1px solid #f1f1f1;
            }
          }
        } */
         .personal-page .personal-body .task .setting .list-end{
          border-bottom: 5px solid #f1f1f1;
        }
   #my-btn{width: 10rem;height: 2.2rem;background: red;color: #fff;text-align: center;font-size: 0.8rem;border-radius: 0.8rem;margin:0.3rem 5rem;}
</style>