<template>
    <div class="login">
        <div class="login-head">
          <a href="javascript:;" class="cuo" @click="goback"><img src="../assets/img/错号.png" alt=""></a>
        </div>
        <div class="login-content">
            <h1>Welcome Back</h1>
            <ul class="login-ul">
                <!-- <li>
                    <span>手机号</span>
                    <input type="text" id="phone"/>
                    
                </li> -->
                <li>
                    <span>昵称</span>
                    <input type="text" id="phone" v-model="username"/>
                    
                </li>
                <li>
                    <span>密码</span>
                    <input type="text" id="phone"  v-model="password"/>
                    
                </li>
                {{mess}}
            </ul>
            <p></p>
            <a href="javascript:;" class="forget">忘记密码？</a>
            <div class="bottom">
                <input type="button" id="denglu" value="登 录 " @click="login">
                <div class="or">
                    <s>————————</s>&nbsp or &nbsp<s>————————</s>
                </div>
                <router-link to="/reg">
                <input type="button" id="zhuce" value="注 册">
                </router-link>
            </div>
        </div>
        
    </div>
</template>
<script>
export default {
    data(){
        return {
            username:'',
            password:'',
            mess:''
        }
    },
    methods:{
        goback(){
            this.$router.go(-1)
        },
        login(){
            axios({
                 url:'/api/login',
                 method:'post',
                 data:{username:this.username,password:this.password}
            }).then(
                res=>{
                    if(res.data.err ===0){
                        this.$router.push('/my')
                    }else{
                        this.mess = res.data.msg
                    }
                }
            )     
        }
       
    }
}
</script>


<style>
.login{height:100%;padding:0.5rem 1rem;background:url('../assets/img/bjj.jpg') no-repeat;color: #fff;background-size: 100% 100%;}
.login .login-content{height: 29rem;padding:0.8rem;margin-left: 1.5rem;}
.login .login-content p{height: 1rem;}
.login .login-content .forget{color:#fff;margin-top: 0.5rem;}
.login .login-content .login-ul li{width: 100%;height: 2rem;border:1px solid rgba(250, 250, 250, 0.8);border-radius: 0.9rem;margin-top: 1.2rem;padding-left: 0.5rem;color: #fff;}
.login .login-content .login-ul li span{display: inline-block;width: 2rem;border-right:1px solid #fff;padding-left: 0.4rem;}
.login .login-content .login-ul li input{width: 78%;height: 100%;text-indent: 0.6rem;color: #fff;}


.login .login-content .bottom input{margin-top: 1rem;width: 100%;height:1.9rem;font-weight: 800;}
.login .login-content .bottom #zhuce{background: #9ff;margin-top: 0.7rem;}
.login .login-content .bottom #denglu{background: #9f9;margin: 0.7rem 0;}
.login .login-content .bottom .or{padding-left: 1rem;color:rgba(250, 250, 250, 0.8);}

</style>