<template>
    <div class="dynamic">
         <Header :title='"购物车"'></Header>
        <List :list="list" dataName="dongtai"></List>
    </div>
</template>

<script>
import List from '@/components/List';
import Header from '@/components/Header';
export default {
    name:"dongtai",
    data(){
        return{
            list:[]
        }
    },
    components:{List,Header},
    mounted(){
        axios({
            url:'/api/dongtai',
        }).then(
            res=>this.list = res.data.data
        )
    }
    
}
</script>

