<template>
    <div class="detail">
        <p class="tlt" @click="goback"><img src="@/assets/img/左箭头.png" alt=""></p>
        <div class="d-head"><img :src="detail.src" alt=""></div>
        <div class="d-content">
            <h2>{{detail.price}}</h2>
            <p><span>价格￥</span><s>{{detail.money}}</s></p>
            <h3>{{detail.title}}</h3>
            <p><span>发货&nbsp&nbsp&nbsp</span>{{detail.city}} <u>快递：免运费</u>  <span class="xiao">月销<i>{{detail.sales}}</i></span></p>
            <p><span>服务 &nbsp&nbsp</span>  退货*运费险 店家提供售后服务</p>
        </div>
        <div class="choose">
            <ul class="ch-ul">
                <li>
                    <h4>商品尺码</h4>
                    <em>S [80斤以下]</em>
                    <em>M [100-115斤]</em>
                    <em>L [115-135斤]</em>
                    <em>XL [135-145斤]</em>
                    <em>2XL [145-155斤]</em>
                </li>
                 <li>
                    <h4>主要颜色</h4>
                    <em>紫蓝色</em>
                    <em>深灰色</em>
                    <em>米白色</em>
                    <em>黑色</em>
                </li>
            </ul>
        </div>
        <div class="addmun">
            <p>购买数量</p>
            <div class="anniu">
                <span>-</span>
                <input type="text" value="1" id="addanniu">
                <span>+</span>
            </div>
        </div>
        <div class="last">
                <a href="javascript:;" class="jiaru">加入购物车</a>
                <a href="javascript:;" class="goumai">立即购买</a>
        </div>
    </div>
    
</template>
<script>
export default {
    data(){
        return {
            detail:{}
        }
    },
    methods:{
        goback(){
            this.$router.go(-1)
        }
    },
    mounted(){
        let id = this.$route.params.id;
        let dataName = this.$route.query.dataName;
        // console.log(id,dataName)
        axios({
            url:`/api/${dataName}/${id}`
        }).then(
            res=>this.detail = res.data.data
        )
    }
}
</script>

<style>
.detail{height: 11rem;line-height: 1.3rem;}
.detail .tlt{width: 100%;height:1.9rem;background: #99f;padding-top:0.3rem;}
.detail .d-head img{width: 100%;height: 14rem;}
.detail .d-content {height: 8.5rem;padding:0 1rem;border-bottom: 1px solid #ccc;}
.detail .d-content h2{color: red;}
/* .detail .d-content h2 span{color: red;font-size: 0.7rem;} */
.detail .d-content span{color: rgba(0,0,0,0.6);}
.detail .d-content s{color: rgba(0,0,0,0.6);}
.detail .d-content i{font-style: normal}
.detail .d-content .xiao{float: right;}
.detail .d-content u{margin-left: 1.5rem;}
.detail .choose{height: 10rem;border-bottom:1px solid #ccc;padding:0 1rem; }
.detail .choose em{font-style: normal;display: inline-block;width: 5rem;height:1.3rem;background: rgba(0,0,0,.1);margin:0.2rem;text-align: center}
.detail .addmun{padding:0.8rem 1rem;text-align: center;height: 3.5rem;margin-top: 0.5rem;padding-bottom: 3rem;}
.detail .addmun p{width: 20%;float: left;font-size: 0.65rem;}
.detail .addmun .anniu{float: right;width: 6rem;}
.detail .addmun .anniu span{float:left;display: inline-block;width: 1.3rem;height: 1.4rem;background: silver;font-size: 1.1rem;}
.detail .addmun .anniu #addanniu{width: 1.5rem;height: 1.3rem;border: 1px solid silver;margin-top: -0.3rem;float: left;margin-top: 0.01rem;text-align: center;}
.detail .last{height: 3.5rem;position: fixed;bottom: 0;padding-left: 1.5rem;background:#fff;z-index: 10;}
.detail .last a{display: inline-block;width: 7rem;height: 1.6rem;color: #fff;text-align: center;font-size: 0.8rem;padding-top: 0.3rem;float:left;margin-right: 0.8rem;margin-top: 1rem;}
.detail .last .jiaru{background: gold;}
.detail .last .goumai{background:red;}
</style>
