<template>
    <div class="home">
        <Banner></Banner>
        <HeadNav></HeadNav>
        <Hot :hot="hot" dataName="home"></Hot>
        <Tuijian></Tuijian>
    </div>
   
</template>

<script>
import Banner from '@/components/Banner'
import HeadNav from '@/components/HeadNav'
import Tuijian from '@/components/Tuijian'
import Hot from '@/components/Hot'



export default {
    name:"home",
    data(){
        return{
            hot:[],
        }
    },
     components:{
       Banner,Tuijian,Hot,HeadNav
    },
   mounted(){
       axios({
        //    前端反向代理 vue.config.js
           url:'/api/home',
        //    params:{_page:1,_limit:8}
       }).then(
           res=>this.hot = res.data.data
       )
   }
}
</script>

