<template>
    <div class="reg">
        <div class="reg-head">
          <a href="javascript:;" class="cuo"><img src="../assets/img/错号.png" alt=""></a>
        </div>
        <div class="reg-content">
            <h1>Sign up here</h1>
            <p>precious collection is waiting for you</p>
           <ul class="reg-ul">
                <li>
                    <span>手机号</span>
                    <input type="text" id="phone"/>
                    
                </li>
                <li>
                    <span>昵称</span>
                    <input type="text" id="phone" v-model="username"/>
                    
                </li>
                <li>
                    <span>密码</span>
                    <input type="text" id="phone" v-model="password"/>
                    
                </li>
            </ul>
            <div class="bottom">
                <input type="button" id="zhuce" value="注 册" @click="reg"/>
                <hr>
                <router-link to="/login">
                <input type="button" id="denglu" value="登 录" />
                </router-link>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
   data(){
       return{
           username:'',
           password:''
       }
   } ,
   methods:{
       reg(){
          let formData = new FormData();
          formData.append('username',this.username)
          formData.append('password',this.password)
           axios({
               url:'/api/reg',
               method:'post',
               data:formData
           }).  then(
              res => res.data.err===0 ? this.$router.push('/login') : this.mess = res.data.msg
           )
       }
   }
}
</script>

